import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer, toast } from "react-toastify";

import { drugMakeFetch, updateMake } from "../../../../reduxs/actions/Make";

import SearchMember from "../common/SearchCID";
import Form from "./Form";

import "react-toastify/dist/ReactToastify.css";

function Locaction() {
  const makes = useSelector((state) => state.makes.currentMake);

  // เรียกใช้งาน รีดัก
  const dispatch = useDispatch();
  const [cid, setCid] = useState("");
  const [frmReset, setFrmReset] = useState(false);
  // Confirm From Search
  const onConfirm = (msg, cid) => {
    if (msg === "CID") {
      console.log(cid);
      setCid(cid);
      dispatch(drugMakeFetch(cid));
    }
  };

  const onConfirmForm = (msg, oappId, otpId, otpsId, endDate) => {
    let data = {
      appSecret: "6d0c2f9fe0048287ad82ac72cb4634ef",
      oapp_id: oappId,
      otp_id: otpId,
      otps_id: otpsId,
      new_date: endDate
    };
    // create promise data
    let Create = new Promise(function (resolve, reject) {
      // save health
      async function createTrack() {
        return await dispatch(updateMake(data)).then((res) => {
          return res[0].status;
        });
      }

      // get result by function
      (async function () {
        let resultTrack = await createTrack();
        console.log(resultTrack);
        if (resultTrack === "SUCCESS") {
          resolve(true);
        } else {
          reject(false);
        }
      })();
    });

    // promiss status and msg
    Create.then((status) => {
      if (status) {
        toast.success("ปรับปรุงข้อมูลเรียบร้อย", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined
        });
      } else {
        toast.warning("บันทึกข้อมูลผิดพลาด!", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined
        });
      }
    }).catch((e) => {
      toast.error("บันทึกข้อมูลผิดพลาด!", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      });
    });

    // reload page
    let timeout = new Promise((resolve, reject) => {
      setFrmReset(false);
      setCid("");
      setTimeout(() => {
        resolve(true);
      }, 3000);
    });

    // promiss status and msg
    timeout
      .then((status) => {
        setFrmReset(true);
        // dispatch(drugMakeFetch(cid));
      })
      .catch((e) => {
        //console.log(e);
      });
  };

  useEffect(() => {
    console.log("Reload ...");
  }, [dispatch]);

  return (
    <section className="content" style={{ marginTop: -16 }}>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <SearchMember confirm={onConfirm} reset={frmReset} />
            <Form confirmform={onConfirmForm} reset={frmReset} />
          </div>
        </div>
      </div>
      <ToastContainer />
    </section>
  );
}

export default Locaction;
