import React, { useState, useEffect } from "react";
import classNames from "classnames";
function SearchCID(props) {
  /**
   * State
   */
  const initialState = {
    cid: { value: "", isValid: true }
  };
  const [data, setData] = useState(initialState);

  // vilid from in input
  const formIsValid = () => {
    let isGood = true;
    if (data.cid.value === "") {
      data.cid.isValid = false;
      isGood = false;
    }

    if (!isGood) {
      setData({
        ...data
      });
    }
    return isGood;
  };

  // handle onclick submit
  const handleSubmit = (e) => {
    e.preventDefault();

    if (formIsValid()) {
      props.confirm("CID", data.cid.value);
    }
  };

  useEffect(() => {
    if (props.reset) {
      // console.log(initialState);
      setData((data) => ({
        ...data,
        cid: { value: "" }
      }));
    }
  }, [props.reset]);
  /**
   * View
   */
  return (
    <div className="row">
      <div className="col-12">
        {/* <div className="card collapsed-card"> */}
        <div className="card">
          <div className="card-header">
            <i className="fas fa-search" /> ค้นหา
            {/* <div className="card-tools">
              <button
                type="button"
                className="btn btn-tool"
                data-card-widget="collapse"
              >
                <i className="fas fa-plus" />
              </button>
            </div> */}
          </div>
          <div className="card-body">
            <form onSubmit={(e) => handleSubmit(e)}>
              <div className="row">
                <div className="col-md-6 offset-md-2">
                  <div className="form-group">
                    <input
                      type="search"
                      className={classNames("form-control ", {
                        "form-control  is-invalid": data.cid.isValid === false
                      })}
                      placeholder="กรุณากรอก เลขบัตร ปปช"
                      value={data.cid.value}
                      name="hn"
                      onChange={(e) =>
                        setData({
                          ...data,
                          cid: { value: e.target.value }
                        })
                      }
                    />

                    
                  </div>
                </div>
                <div className="col-md-2 col-12">
                  <div className="form-group">
                    <button type="submit" className="btn btn-block btn-primary">
                      <i className="fas fa-search" /> ค้นหา
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SearchCID;
