import React, { useState, useEffect } from "react";
// import classNames from "classnames";
import { useSelector } from "react-redux";
import moment from "moment";
import { toast } from "react-toastify";
import DatePicker from "react-datepicker";
import { registerLocale } from "react-datepicker";
/**
 * Style
 */
import "react-toastify/dist/ReactToastify.css";
import "react-datepicker/dist/react-datepicker.css";

import th from "date-fns/locale/th";
import "moment/locale/th.js";
moment.locale("th");
registerLocale("th", th);

const Form = (props) => {
  // const makesStatus = useSelector((state) => state.makes.statuscid);
  const makes = useSelector((state) => state.makes.currentMake);

  const [name, setName] = useState("");
  const [hn, setHn] = useState("");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState(new Date());
  const [oappId, setOappId] = useState("");
  const [otpId, setOtpId] = useState("");
  const [otpsId, setOtpsId] = useState("");

  // handle confirm on props with record component
  const onConfirmClick = () => {
    if (props.confirmform) {
      const msg = "SearchList";
      props.confirmform(
        msg,
        oappId,
        otpId,
        otpsId,
        moment(endDate).format("YYYY-MM-DD HH:mm")
      );
    } else {
      props.confirmform("");
    }
  };
  // handle from validate
  const formIsValidSearch = () => {
    let isGood = true;

    if (endDate === "" || endDate === null) {
      isGood = false;
    }
    if (name === "") {
      isGood = false;
    }
    if (hn === "") {
      isGood = false;
    }
    if (startDate === "") {
      isGood = false;
    }
    if (!isGood) {
      // setData({
      //   ...data
      // });
    }
    return isGood;
  };
  // handle onclick submit
  const handleSubmit = (e) => {
    e.preventDefault();
    if (formIsValidSearch()) {
      console.log(oappId, otpId, otpsId, endDate);
      onConfirmClick();
    } else {
      toast.warning("บันทึกข้อมูลผิดพลาด กรุณากรอก เลข บัตรประชาชน!", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined
      });
    }
  };

  //ฟังก์ชั่น กำหนดข้อมูล เริ่มต้น
  const isSetData = () => {
    const initialVariable = {
      fullname: "",
      hn: "",
      date_start: "",

      oapp_id: "",
      otp_id: "",
      otps_id: ""
    };

    // กรณี รีเซต ฟอร์ม
    if (props.reset) {
      return { initialVariable };
    } else {
      // รับข้อมูล จาก รีดัก
      makes.map((rs) => {
        // กรณีไม่มีข้อมูล
        if (rs.results.length === 0) {
          return { initialVariable };
        }
        // กรณีมีข้อมูล
        else {
          console.log(makes);
          // กำหนด ข้อมูล ให้กับตัวแปร
          return rs.results.map((rs) => {
            initialVariable.fullname = rs.full_name;
            initialVariable.hn = rs.hn;
            initialVariable.date_start = moment(rs.nextdate).format(
              "YYYY-MM-DD"
            );

            initialVariable.oapp_id = rs.oapp_id;
            initialVariable.otps_id = rs.otps_id;
            initialVariable.otp_id = rs.otp_id;
            return { initialVariable };
          });
        }
      });
    }
    return { initialVariable };
  };

  const { initialVariable } = isSetData();

  useEffect(() => {
    setHn(initialVariable.hn);
    setName(initialVariable.fullname);
    setStartDate(initialVariable.date_start);
    setOappId(initialVariable.oapp_id);
    setOtpId(initialVariable.otp_id);
    setOtpsId(initialVariable.otps_id);
  }, [
    makes,
    props.reset,
    initialVariable.hn,
    initialVariable.fullname,
    initialVariable.date_start,
    initialVariable.oapp_id,
    initialVariable.otps_id,
    initialVariable.otp_id
  ]);

  return (
    <div className="row">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-12">
                <form onSubmit={(e) => handleSubmit(e)}>
                  <div className="row">
                    <div className="col-md-6 col-12">
                      <div className="form-group">
                        <label htmlFor="InputMember" className="col-form-label">
                          {" "}
                          ชื่อ-สกุล
                        </label>
                        <input
                          type="text"
                          className="form-control "
                          placeholder="กรุณากรอก ชื่อ-สกุล"
                          value={name}
                          name="name"
                          // onChange={(e) =>
                          //   setData({
                          //     ...data,
                          //     cid: { value: e.target.value }
                          //   })
                          // }
                          disabled
                        />
                      </div>
                    </div>
                    <div className="col-md-6 col-12">
                      <div className="form-group">
                        <label
                          htmlFor="InputMemberName"
                          className="col-form-label"
                        >
                          {" "}
                          HN
                        </label>
                        <input
                          type="text"
                          className="form-control "
                          placeholder="กรุณากรอก HN"
                          value={hn}
                          name="hn"
                          // onChange={(e) =>
                          //   setData({
                          //     ...data,
                          //     cid: { value: e.target.value }
                          //   })
                          // }
                          disabled
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-6 col-12">
                      <div className="form-group">
                        <label htmlFor="InputMember" className="col-form-label">
                          {" "}
                          วันที่นัด
                        </label>
                        <DatePicker
                          locale="th"
                          className="form-control"
                          dateFormat="dd LLLL yyyy"
                          // selected={startDate}
                          selected={
                            startDate !== "" ? new Date(startDate) : startDate
                          }
                          onChange={(date) => setStartDate(date)}
                          placeholderText="กรุณาเลือกวันที่วันที่นัด"
                          disabled
                        />
                      </div>
                    </div>
                    <div className="col-md-6 col-12">
                      <div className="form-group">
                        <label
                          htmlFor="InputMemberName"
                          className="col-form-label"
                        >
                          {" "}
                          วันที่เลื่อนนัด
                        </label>
                        <DatePicker
                          locale="th"
                          className="form-control"
                          isClearable={endDate !== "" ? true : false}
                          selected={
                            endDate !== "" ? new Date(endDate) : endDate
                          }
                          dateFormat="dd LLLL yyyy"
                          // selected={endDate}
                          onChange={(date) => setEndDate(date)}
                          placeholderText="กรุณาเลือกวันที่เลื่อนนัด"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-12 col-12">
                      <div className="form-group" style={{ textAlign: "end" }}>
                        <button type="submit" className="btn btn-primary">
                          ปรับปรุงข้อมูล
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          {/* <div className="overlay">
            <b className="text-muted">
              กรุณากรอกเลข บัตรประชาชน เพื่อทำการบันทึกข้อมูล
            </b>
          </div> */}
        </div>
      </div>
    </div>
  );
};

export default Form;
